                     @click="CopyLink">
                  <div>
                    <div class="q-avatar" style="">
                      <div class="q-avatar__content row flex-center overflow-hidden" style=""><img class="q-pa-xs"
                                                                                                   src="data:image/png;base64,">
                      </div>
                    </div>
                  </div>
                  <div class="q-pt-xs q-pb-sm t-font-a"></div>
                </div><!---->
                <div class="col-1"></div>
              </div>
            </div><!----></div>
        </div>
      </div>
    </div>
  </div>

</template>


<script>
import Carousel from "../components/Carousel";
import Kimage from "../assets/kakao-pay-black.c698e475.png"
import image1 from "../assets/gallery/HYW09498.jpg"
import image2 from "../assets/gallery/HYW09305.jpg"
import image3 from "../assets/gallery/HYW09384.jpg"
import image4 from "../assets/gallery/HYW09470.jpg"
import image5 from "../assets/gallery/HYW09542.jpg"
import image6 from "../assets/gallery/HYW09536.jpg"

export default {
  name: "Main",
  components: {
    Carousel,
    'Map' :() => import('@/components/Map'),
  },
  data() {
    return {
      calling: false,
      Dday: '',
      nowLink: '',
      copyData: [],
      btnClick: false,
      btnClick1: false,
      curPos: 0,
      postion: 0,
      start_x: 0,
      end_x: 0,
      IMAGE_WIDTH: 0,
      images: null,
      KakaoImage: [
        Kimage
      ],
      imageUrls: [
        image3,
        image1,
        image2,
        image4,
        image5,
        image6
      ],
    };
  }, created() {
    this.diffDay()
  },
  computed: {
    getImageWidth: () => {
      const imgWidth = document.querySelector(".images").offsetWidth;
      return imgWidth;
    },
  },
  mounted() {
    this.test()
    console.log(this.Dday)
    this.nowLink = document.location.href;
    console.log(this.$refs.getData)
    console.log(this.$refs.getData1)
    this.copyData.push(this.$refs.getData.innerHTML);
    this.copyData.push(this.$refs.getData1.innerHTML);
    this.IMAGE_WIDTH = this.getImageWidth;
    this.images = document.querySelector(".images");
    this.images.addEventListener("touchstart", this.touch_start);
    this.images.addEventListener("touchend", this.touch_end);
  },
  methods: {
    test() {
      const eventCard = document.getElementById("event-card");


      eventCard.addEventListener("touchstart",function () {
        document.body.style.overflow = "hidden";
      })
      eventCard.addEventListener("touchend",function () {
        document.body.style.overflow = "auto";
      })


    },
    Call() {
      this.calling === !true ? this.calling = true : this.calling = false
    },
    diffDay() {
      var Dday = new Date(2022, 10, 27);    // D-day(2017 8 30) .
      var now = new Date();                    // ()  .
      var gap = now.getTime() - Dday.getTime();    //   D-day  .
      var result = Math.floor(gap / (1000 * 60 * 60 * 24)) * -1;    // gap ( *  *  * ) .   -1     .
      this.Dday = result;
    },
    CopyLink() {
      this.$copyText(this.nowLink).then(function (e) {
        alert("  .\n" + e.text);
      });
    },
    Copy() {
      console.log()
      this.$copyText(this.copyData[0]).then(function (e) {
        alert(" .\n" + e.text);
      });
    }, Copy1() {
      console.log()
      this.$copyText(this.copyData[1]).then(function (e) {
        alert(" .\n" + e.text);
      });
    },
    kakaoLink() {
      window.Kakao.Link.sendDefault({
            objectType: 'feed',
            content: {
              title: ' &  .',
              description: '2022 11 27  10 30 \n  5 ',
              imageUrl:
                  'http://drive.google.com/uc?export=view&id=1-CweZ1mIoQjjSLFbgdU0ElvKp8sfz3ns',
              link: {
                mobileWebUrl: 'https://ukkidoki.github.io/wedding/',
                webUrl: 'https://ukkidoki.github.io/wedding/',
              },
            },
            buttons: [
              {
                title: ' ',
                link: {
                  mobileWebUrl: 'https://ukkidoki.github.io/wedding/',
                  webUrl: 'https://ukkidoki.github.io/wedding/',
                },
              }
            ]
          }
      )
    },
    showPay() {
      this.btnClick != true ? this.btnClick = true : this.btnClick = false;
    }
    ,
    showPay1() {
      this.btnClick1 != true ? this.btnClick1 = true : this.btnClick1 = false;
    }
    ,
    prev() {
      if (this.curPos > 0) {
        this.postion += this.IMAGE_WIDTH;
        this.images.style.transform = `translateX(${this.postion}px)`;
        this.curPos = this.curPos - 1;
      }
    }
    ,
    next() {
      if (this.curPos < this.imageUrls.length - 1) {
        this.postion -= this.IMAGE_WIDTH;
        this.images.style.transform = `translateX(${this.postion}px)`;
        this.curPos = this.curPos + 1;
      }
    }
    ,

    touch_start(event) {
      this.start_x = event.touches[0].pageX;
    }
    ,

    touch_end(event) {
      this.end_x = event.changedTouches[0].pageX;
      if (this.start_x > this.end_x) this.next();
      else this.prev();
    }
  }
}
</script>
<style>
@import "../css/vendor.dbb2073d.css";
@import "../css/app.c2aa9cf6.css";
@import "../css/991.dc6424ff.css";

